import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Link } from 'react-router-dom';
import IconButton from '@material-ui/core/IconButton';
import SaveTheBg from './images/savethedatebg.jpg';
import Leaf from './images/hesonehp.png';
import Gallery1 from './images/gallery1.jpg';
import Gallery2 from './images/gallery2.jpg';
import Gallery3 from './images/gallery3.jpg';
import MenuIcon from '@material-ui/icons/Menu';
//for drawer
import clsx from 'clsx';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import LanguageIcon from '@material-ui/icons/Language';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <span className="copyrightLink" component={Link} to="/">
        Mike &amp; Mel
      </span>
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  hundo: {
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
  },
  heroContent: {
    display: 'flex',
    flex: '1',
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
    alignItems: 'center',
    justifyContent: 'center',
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    // flexGrow: 1,
    color: 'white !important',
    '&:hover': {
      color: '#ffefee !important',
    },
  },
  leftTitle: {
    [theme.breakpoints.up('md')]: {
      borderRight: '1px black solid',
    },
    '& h4': {
      textTransform: 'uppercase',
      fontFamily: "'Cormorant Garamond', serif !important",
    },
  },
  // for the drawer
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
}));

const heroStyle = {
  backgroundImage: `url(${SaveTheBg})`,
  backgroundPosition: 'center',
  backgroundSize: 'cover',
};

export default function English() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
  });
  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {/* {['English', 'Français'].map((text, index) => (
          <ListItem
            button
            key={text}
            component={Link}
            to={text.substring(0, 2).toLowerCase()}
          >
            <ListItemIcon>
              <LanguageIcon />
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))} */}
        <ListItem
          button
          key={'English'}
          component={Link}
          to={'English'.substring(0, 2).toLowerCase()}
        >
          <ListItemIcon>
            <LanguageIcon />
          </ListItemIcon>
          <ListItemText primary={'English'} />
        </ListItem>
        <ListItem
          button
          key={'Français'}
          component={Link}
          to={'Français'.substring(0, 2).toLowerCase()}
        >
          <ListItemIcon>
            <LanguageIcon />
          </ListItemIcon>
          <ListItemText primary={'Français'} />
        </ListItem>
      </List>
      <Divider />
    </div>
  );

  return (
    <React.Fragment>
      <CssBaseline />
      <div className={classes.root}>
        <div className={classes.hundo}>
          <AppBar position="relative">
            <Toolbar>
              {/* <Grid
                justify="space-between" // Add it here :)
                container
                spacing={24}
              >
                <Grid item> */}
              <IconButton
                edge="start"
                className={classes.menuButton}
                color="inherit"
                aria-label="menu"
                onClick={toggleDrawer('left', true)}
              >
                <MenuIcon />
              </IconButton>

              <Drawer
                anchor={'left'}
                open={state['left']}
                onClose={toggleDrawer('left', false)}
              >
                {list('left')}
              </Drawer>
              <Typography
                variant="h6"
                className={classes.title}
                component={Link}
                to="/"
              >
                MÉLYSSA &amp; MICHAEL
              </Typography>
              {/* </Grid> */}
              {/* <Grid item>
                  <Link to="/en" color="inherit">
                    <span className="tool-languages">English</span>
                  </Link>

                  <Link to="/fr" className="tool-languages" color="inherit">
                    <span className="tool-languages">Français</span>
                  </Link>
                </Grid> */}
              {/* </Grid> */}
            </Toolbar>
          </AppBar>
          {/* <Typography variant="h6" color="inherit" noWrap>
            MÉLYSSA &amp; MICHAEL
          </Typography>
          <Link to="/en" color="inherit">
            English
          </Link>
          <Link to="/fr " color="inherit">
            Français
          </Link> */}
          {/* Hero unit */}
          <div style={heroStyle} className={classes.heroContent}>
            <Container maxWidth="sm">
              <Typography
                component="h1"
                variant="h2"
                align="center"
                gutterBottom
                className="savethedate"
              >
                <span style={{ color: 'white' }}>Save the date!</span>
              </Typography>
              <Typography
                variant="h5"
                align="center"
                color="textSecondary"
                paragraph
                className="savedate"
              >
                <span style={{ color: 'white' }}>AUGUST 14, 2021</span>
              </Typography>

              {/* <div className={classes.heroButtons}>
                <Grid container spacing={2} justify="center">
                  <Grid item>
                    <Button variant="contained" color="primary">
                      Main call to action
                    </Button>
                  </Grid>
                  <Grid item>
                    <Button variant="outlined" color="primary">
                      Secondary action
                    </Button>
                  </Grid>
                </Grid>
              </div> */}
            </Container>
          </div>
        </div>
      </div>
      <div className="names">
        <img
          className="topLeaf"
          style={{ width: '415px', maxWidth: '100%' }}
          src={Leaf}
          alt="Leaf"
        />
        <Typography className="fullNames" component="h3" variant="h2">
          Mélyssa Mckay-Nicole
          <br />
          <span className="smallAnd">and</span>
          <br />
          Michael Doseger
        </Typography>
        <img
          style={{ width: '415px', maxWidth: '100%' }}
          src={Leaf}
          alt="Leaf"
        />
      </div>
      <div className="story">
        <Grid container spacing={0}>
          <Grid className={classes.leftTitle} item xs={12} md={6}>
            <Typography component="h4" variant="h4">
              Our Story
            </Typography>
            <span className="storyDate">August 14, 2010</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>

          <Grid item xs={12} md={4}>
            <span
              style={{
                fontSize: '1.75em',
                maxWidth: '95%',
                display: 'inline-block',
              }}
            >
              This love story started with two 17 years old who thought they
              knew it all. But little did they know, they would go through
              life’s ups and downs and support each other on the road to finding
              themselves in adulthood. They have grown from teens graduating
              from high school to adults who are now building their career and
              have built a whole life together. Although, they have known it for
              a long time now, they are ready to commit to each other for
              Forever&amp;Always.
            </span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>

          <Grid className="gridSpacer" item xs={12}></Grid>
          <Grid className={classes.leftTitle} item xs={12} md={6}>
            <Typography component="h4" variant="h4">
              The Proposal
            </Typography>
            <span className="storyDate">August 14, 2020</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid item xs={12} md={4}>
            <span
              style={{
                fontSize: '1.75em',
                maxWidth: '95%',
                display: 'inline-block',
              }}
            >
              Going through a pandemic and having their trip to Spain cancelled
              where Michael was originally planning to ask Melyssa, he had to
              scramble to come up with a new idea. Proposing on their 10 year
              anniversary seemed like the next best thing. Going to take
              “pictures” on the Lachine lakeshore, Michael completely took
              Melyssa by surprise, although she had been given little hints
              throughout the week, making it a perfect proposal. They now hope
              you will join them for the perfect celebration of their love on
              their 11 year anniversary on August 14th 2021.
            </span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>

          <Grid className="gridSpacer" item xs={12}></Grid>
        </Grid>
        <Grid container spacing={0}>
          <Grid className="gridSpacer" item xs={12}></Grid>
          <Grid item xs>
            <img
              className="galleryImagesLg"
              src={Gallery1}
              alt="Mike and Mel"
            />
            <img
              className="galleryImagesLg"
              src={Gallery2}
              alt="Mike and Mel"
            />
            <img
              className="galleryImagesLg"
              src={Gallery3}
              alt="Mike and Mel"
            />
          </Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
        </Grid>
      </div>
      <div></div>
      {/* Footer */}
      <footer className={classes.footer}>
        <Typography variant="h6" align="center" gutterBottom>
          <span style={{ fontSize: '1.5rem' }}>
            We can't wait to celebrate with you!
          </span>
        </Typography>
        <Typography
          variant="subtitle1"
          align="center"
          color="textSecondary"
          component="p"
        >
          <span className="footerSignature">Mélyssa &amp; Michael</span>
        </Typography>
        <Copyright />
      </footer>
      {/* End footer */}
    </React.Fragment>
  );
}
