import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Link } from 'react-router-dom';
import IconButton from '@material-ui/core/IconButton';
import SaveTheBg from './images/savethedatebg.jpg';
import Leaf from './images/hesonehp.png';
import Gallery1 from './images/gallery1.jpg';
import Gallery2 from './images/gallery2.jpg';
import Gallery3 from './images/gallery3.jpg';
import MenuIcon from '@material-ui/icons/Menu';
//for drawer
import clsx from 'clsx';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import LanguageIcon from '@material-ui/icons/Language';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <span className="copyrightLink">
        <Link href="mikeandmelyssawedding.com">Mike &amp; Mel</Link>{' '}
      </span>
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  hundo: {
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
  },
  heroContent: {
    display: 'flex',
    flex: '1',
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
    alignItems: 'center',
    justifyContent: 'center',
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    // flexGrow: 1,
    color: 'white !important',
    '&:hover': {
      color: '#ffefee !important',
    },
  },
  leftTitle: {
    [theme.breakpoints.up('md')]: {
      borderRight: '1px black solid',
    },
    '& h4': {
      textTransform: 'uppercase',
      fontFamily: "'Cormorant Garamond', serif !important",
    },
  },
  // for the drawer
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
}));

const heroStyle = {
  backgroundImage: `url(${SaveTheBg})`,
  backgroundPosition: 'center',
  backgroundSize: 'cover',
};

export default function French() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
  });
  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {/* {['English', 'Français'].map((text, index) => (
          <ListItem
            button
            key={text}
            component={Link}
            to={text.substring(0, 2).toLowerCase()}
          >
            <ListItemIcon>
              <LanguageIcon />
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))} */}
        <ListItem
          button
          key={'English'}
          component={Link}
          to={'English'.substring(0, 2).toLowerCase()}
        >
          <ListItemIcon>
            <LanguageIcon />
          </ListItemIcon>
          <ListItemText primary={'English'} />
        </ListItem>
        <ListItem
          button
          key={'Français'}
          component={Link}
          to={'Français'.substring(0, 2).toLowerCase()}
        >
          <ListItemIcon>
            <LanguageIcon />
          </ListItemIcon>
          <ListItemText primary={'Français'} />
        </ListItem>
      </List>
      <Divider />
    </div>
  );

  return (
    <React.Fragment>
      <CssBaseline />
      <div className={classes.root}>
        <div className={classes.hundo}>
          <AppBar position="relative">
            <Toolbar>
              {/* <Grid
                justify="space-between" // Add it here :)
                container
                spacing={24}
              >
                <Grid item> */}
              <IconButton
                edge="start"
                className={classes.menuButton}
                color="inherit"
                aria-label="menu"
                onClick={toggleDrawer('left', true)}
              >
                <MenuIcon />
              </IconButton>

              <Drawer
                anchor={'left'}
                open={state['left']}
                onClose={toggleDrawer('left', false)}
              >
                {list('left')}
              </Drawer>
              <Typography
                variant="h6"
                className={classes.title}
                component={Link}
                to="/"
              >
                MÉLYSSA &amp; MICHAEL
              </Typography>
              {/* </Grid> */}
              {/* <Grid item>
                  <Link to="/en" color="inherit">
                    <span className="tool-languages">English</span>
                  </Link>

                  <Link to="/fr" className="tool-languages" color="inherit">
                    <span className="tool-languages">Français</span>
                  </Link>
                </Grid> */}
              {/* </Grid> */}
            </Toolbar>
          </AppBar>
          {/* <Typography variant="h6" color="inherit" noWrap>
            MÉLYSSA &amp; MICHAEL
          </Typography>
          <Link to="/en" color="inherit">
            English
          </Link>
          <Link to="/fr " color="inherit">
            French
          </Link> */}
          {/* Hero unit */}
          <div style={heroStyle} className={classes.heroContent}>
            <Container maxWidth="sm">
              <Typography
                component="h1"
                variant="h2"
                align="center"
                color="textPrimary"
                gutterBottom
                className="savethedate"
              >
                <span style={{ color: 'white' }}>Réservez la date!</span>
              </Typography>
              <Typography
                variant="h5"
                align="center"
                color="textSecondary"
                paragraph
                className="savedate"
              >
                <span style={{ color: 'white' }}>14 août 2021</span>
              </Typography>
              {/* <div className={classes.heroButtons}>
                <Grid container spacing={2} justify="center">
                  <Grid item>
                    <Button variant="contained" color="primary">
                      Main call to action
                    </Button>
                  </Grid>
                  <Grid item>
                    <Button variant="outlined" color="primary">
                      Secondary action
                    </Button>
                  </Grid>
                </Grid>
              </div> */}
            </Container>
          </div>
        </div>
      </div>
      <div className="names">
        <img
          className="topLeaf"
          style={{ width: '415px', maxWidth: '100%' }}
          src={Leaf}
          alt="Leaf"
        />
        <Typography className="fullNames" component="h3" variant="h2">
          Mélyssa Mckay-Nicole
          <br />
          <span className="smallAnd">et</span>
          <br />
          Michael Doseger
        </Typography>
        <img
          style={{ width: '415px', maxWidth: '100%' }}
          src={Leaf}
          alt="Leaf"
        />
      </div>

      <div className="story">
        <Grid container spacing={0}>
          <Grid className={classes.leftTitle} item xs={12} md={6}>
            <Typography component="h4" variant="h4">
              Notre histoire
            </Typography>
            <span className="storyDate">14 août 2010</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid item xs={12} md={4}>
            <span
              style={{
                fontSize: '1.75em',
                maxWidth: '95%',
                display: 'inline-block',
              }}
            >
              Cette histoire d'amour a commencé avec deux jeunes de 17 ans qui
              pensaient tout savoir. Mais ce qu’ils ne savaient pas, c’est
              qu’ils s’apprêtaient ensemble à traverser les hauts et les bas de
              la vie et qu’ils se soutiendraient à travers toutes les épreuves.
              Ils sont passés d'adolescents sortant du secondaire à des adultes
              qui construisent maintenant leur carrière ainsi que leur vie
              entière ensemble. Bien qu'ils le sachent depuis longtemps
              maintenant, ils sont maintenant prêts à s'engager l'un envers
              l'autre pour toujours et à jamais.
            </span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
          <Grid className={classes.leftTitle} item xs={12} md={6}>
            <Typography component="h4" variant="h4">
              La grande demande
            </Typography>
            <span className="storyDate">14 août 2020</span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid item xs={12} md={4}>
            <span
              style={{
                fontSize: '1.75em',
                maxWidth: '95%',
                display: 'inline-block',
              }}
            >
              Traversant une pandémie et ayant du annulé leur voyage en Espagne
              où Michael avait initialement prévu de demander à Melyssa, il a dû
              faire preuve d’imagination pour trouver une nouvelle idée.
              Proposer le jour de leur 10e anniversaire semblait être la
              meilleure option. Allant prendre des «photos» sur les bord du lac
              à Lachine, Michael a complètement pris Mélyssa par surprise, même
              si elle avait reçu de petits indices tout au long de la semaine,
              ce qui en a fait une parfaite grande demande. Ils espèrent
              maintenant que vous les rejoindrez pour la célébration de leur
              amour à l'occasion de leur 11e anniversaire le 14 août 2021.
            </span>
          </Grid>
          <Grid item xs={12} md={1}></Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
        </Grid>
        <Grid container spacing={0}>
          <Grid className="gridSpacer" item xs={12}></Grid>
          <Grid item xs>
            <img
              className="galleryImagesLg"
              src={Gallery1}
              alt="Mike and Mel"
            />
            <img
              className="galleryImagesLg"
              src={Gallery2}
              alt="Mike and Mel"
            />
            <img
              className="galleryImagesLg"
              src={Gallery3}
              alt="Mike and Mel"
            />
          </Grid>
          <Grid className="gridSpacer" item xs={12}></Grid>
        </Grid>
      </div>

      <div></div>
      {/* Footer */}
      <footer className={classes.footer}>
        <Typography variant="h6" align="center" gutterBottom>
          <span style={{ fontSize: '1.5rem' }}>
            Nous avons hâte de célébrer avec vous!
          </span>
        </Typography>
        <Typography
          variant="subtitle1"
          align="center"
          color="textSecondary"
          component="p"
        >
          <span className="footerSignature">Mélyssa &amp; Michael</span>
        </Typography>
        <Copyright />
      </footer>
      {/* End footer */}
    </React.Fragment>
  );
}
